import React, { useEffect, useRef, useContext } from "react";

import Header from "./Header/Header";
import MusicPlayer from "./Main/MusicPlayer";
import SideMenu from './SideMenu/SideMenu';
import { Contexts } from './../context/Context';

const App = () => {

  const { songs,
    menuToggle, setMenuToggle,
    activeSong, setActiveSong,
    isPlaying, setIsPlaying } = useContext(Contexts)

  const audioRef = useRef();

  useEffect(() => {
    if (audioRef.current) {
      audioRef?.current?.play().then(_ => {
        setIsPlaying(true);
      }).catch(err => { });
    }
  }, [activeSong]);

  return (
    <>
      <Header menuToggle={menuToggle} setMenuToggle={setMenuToggle} />
      <MusicPlayer
        songs={songs}
        activeSong={activeSong}
        setActiveSong={setActiveSong}
        audioRef={audioRef}
        isPlaying={isPlaying}
        setIsPlaying={setIsPlaying}
      />
      <SideMenu />
    </>
  );
}

export default App;