import React from "react";

import { Box } from '@mui/system';
import MusicCover from "./Music/MusicCover";
import MusicDetails from "./Music/MusicDetails";
import MusicControls from "./Music/MusicControls";

const MusicPlayer = (props) => {
    return (
        <Box
            component="main"
            position="fixed"
            top="5rem"
            width="100%"
            height="calc(100vh - 5rem)"
            backgroundColor="darkslategray"
            display="flex"
            flexDirection="column"
            alignItems="center"
        >
            <MusicCover activeSong={props.activeSong} />
            <MusicDetails activeSong={props.activeSong} />
            <MusicControls
                songs={props.songs}
                activeSong={props.activeSong}
                audioRef={props.audioRef}
                isPlaying={props.isPlaying}
                setIsPlaying={props.setIsPlaying}
                setActiveSong={props.setActiveSong}
            />
        </Box>
    );
}

export default MusicPlayer;